import { useContext } from 'react';
import { CartContext } from '../contexts/CartContext';
import { Col, Container, Row } from "react-bootstrap";
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import "./Watch.css";

function Watchmen({ onAddToCart }) {
  const { addToCart } = useContext(CartContext);
  const watches = [
    { id: "FC-718WNWM4H6", name: "Frederique Constant Vietnam Limited Edition 2025", type: "42 mm | ng h c (Mechanical)", price: "168.000.000", img: "/img/dh1.webp" },
    { id: "RE-AT0018S00B", name: "ng h Nam Orient Star Phin bn gii hn 2025", type: "39.3 mm | ng h c (Mechanical)", price: "28.800.000", img: "img/dh2.webp" },
    { id: "RA-AS0105S30B", name: "ng h Nam Orient Phin bn c bit 2025", type: "41.5 mm | ng h c (Mechanical)", price: "16.800.000", img: "img/dh3.webp" },
    { id: "RA-AS0106L30B", name: "ng h Nam Orient Phin bn c bit 2025", type: "41.5 mm | ng h c (Mechanical)", price: "16.000.000", img: "img/dh4.webp" },
    { id: "FC-303TA3DNH6", name: "ng H Nam Frederique Constant HIGHLIFE", type: "33 mm | ng h in t (Quartz)", price: "8.900.000", img: "img/dh5.webp" },
    { id: "RA-AK0802S30B", name: "ng H Nam ORIENT Classic Sun and Moon", type: "41.5 mm | ng h c (Mechanical)", price: "10.000.000", img: "img/dh11.webp" },
    { id: "C032.451.11.047.00", name: "ng H Nam Certina DS Action Day-Date", type: "43 mm | ng h in t (Quartz)", price: "16.900.000", img: "img/dh12.webp" },
    { id: "RE-AY0121A00B", name: "ng h Nam Orient Star Classic Moonphase", type: "41 mm | ng h c (Mechanical)", price: "55.900.000", img: "img/dh13.webp" }
  ];
  return (
    <Container className="watch-list mt-4">
      <h2 className="title fw-bold">B su tp ng h nam chnh hng ti ng h Galle</h2>
      <hr />
      <Row>
        {watches.map((watch) => (
          <Col key={watch.id} xs={12} sm={6} md={4} lg={3} className="mb-4">
            <Card className="watch-card h-100 shadow-sm">
              <Card.Img
                variant="top"
                src={watch.img}
                alt={watch.name}
                className="watch-img"
              />
              <Card.Body className="d-flex flex-column">
                <Card.Title className="watch-title">{watch.name}</Card.Title>
                <Card.Text className="watch-text">
                  <strong>M:</strong> {watch.id} <br />
                  <p>{watch.type}</p>
                  <strong>Gi:</strong> {watch.price}
                </Card.Text>
                <div className="mt-auto">
                  <Button
                    variant="primary"
                    className="watch-btn"
                    onClick={() => {
                      addToCart(watch);
                      if (onAddToCart) onAddToCart();
                    }}
                  >
                    Thm vo gi
                  </Button>
                </div>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export default Watchmen;