import React, { useContext } from "react";
import { Link } from "react-router-dom";
import NavDropdown from 'react-bootstrap/NavDropdown';
import "./Header.css";
import { CartContext } from '../contexts/CartContext';
import Account from "./Account";

function Header({ onCartClick }) {
  const { cartItems } = useContext(CartContext); // Thm dng ny

  // Tnh tng s lng sn phm trong gi
  const uniqueTypes = new Set(cartItems.map(item => item.id));
  const totalTypes = uniqueTypes.size;
  // Thm dng ny

  function Header({ onCartClick }) {
  const { cartItems } = useContext(CartContext);

  const uniqueTypes = new Set(cartItems.map(item => item.id));
  const totalTypes = uniqueTypes.size;
  }
  return (
    <header className="header1">
      <div className="container">
        {/* Header Top */}
        <div className="header1-top">
          {/* Tri */}
          <div className="header1-left">
            <Link to="/store-system" className="item">
              <img
                src="https://galle.vn/images/config/location_1718256784.svg"
                alt="Store"
              />
              <span>H THNG CA HNG</span>
            </Link>
            <a href="tel:18006785" className="item">
              <img
                src="https://galle.vn/images/config/call-dark_1718256807.svg"
                alt="Hotline"
              />
              <span>1800 6785</span>
            </a>
          </div>

          {/* Logo */}
          <div className="header1-logo">
            <Link to="/">
              <img
                src="https://galle.vn/images/config/logo_1718252240.svg"
                alt="Galle Watch"
              />
            </Link>
          </div>

          {/* Phi */}
          <div className="header1-right">
            <div className="search-box">
              <input type="text" placeholder="Tm Kim" />
              <button>
                <img src="/img/seach.jpg" height="24" alt="Search" />
              </button>
            </div>
            <Account />
            <a href="#">
              <img
                src="https://galle.vn/images/config/favorite-dark_1718256807.svg"
                alt="Favorite"
              />
            </a>
            {/* Gi hng */}
            <a onClick={onCartClick} className="cart-icon-btn" style={{ position: 'relative', display: 'inline-block' }}>
              {totalTypes > 0 && (
                <span
                  style={{
                    position: 'absolute',
                    top: '-6px',
                    right: '-13px',
                    background: '#b32d1a',
                    color: '#fff',
                    borderRadius: '50%',
                    padding: '2px 6px',
                    fontSize: '12px',
                    fontWeight: 'bold',
                    border: '2px solid #fff',
                    minWidth: '18px',
                    height: '18px',
                    lineHeight: '14px',
                    textAlign: 'center',
                    boxSizing: 'border-box',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                  }}
                >
                  {totalTypes}
                </span>
              )}
              <img
                src="https://galle.vn/images/config/bag-dark_1718256807.svg"
                alt="Cart"
                
              />
            </a>
          </div>
        </div>
      </div>
      {/* Nav */}
      <nav className="nav">
        <div className="container nav__container">
          <ul className="nav__list">
            <li className="nav__item">
              <Link to="/" className="nav__link">
                <img src="/img/trangchu.png" alt="Trang ch" height="20" />
                Home
              </Link>
            </li>
            <li className="nav__item">
              <NavDropdown
                title={
                  <span className="nav__link">
                    <img src="/img/thuonghieu.png" alt="Thng hiu" height="20" style={{ marginRight: "6px" }} />
                    Thng hiu
                  </span>
                }
                id="basic-nav-dropdown"
              >
                <NavDropdown.Item as={Link} to="/casio"><img src="/img/brand1.webp" alt="Casio" style={{ width: "100px", height: "auto" }} /></NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/garmin"><img src="/img/brand2.webp" alt="Garmin" style={{ width: "100px", height: "auto" }} /></NavDropdown.Item>
              </NavDropdown>
            </li>
            <li className="nav__item">
              <NavDropdown
                title={
                  <span className="nav__link">
                    <img src="/img/donghodeotay.png" alt="ng h eo tay" height="20" style={{ marginRight: "6px" }} />
                    ng h eo tay
                  </span>
                }
                id="basic-nav-dropdown"
              >
                <NavDropdown.Item as={Link} to="/watchmen">ng h nam</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/watchwomen">ng h n</NavDropdown.Item>
              </NavDropdown>
            </li>
            <li className="nav__item">
              <NavDropdown
                title={
                  <span className="nav__link">
                    <img src="/img/donghotreotuong.png" alt="ng h trang tr" height="20" style={{ marginRight: "6px" }} />
                    ng h trang tr
                  </span>
                }
                id="basic-nav-dropdown"
              >
                <NavDropdown.Item as={Link} to="/wallclock">ng h treo tng</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/deskclock">ng h  bn</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/treeclock">ng h cy</NavDropdown.Item>
              </NavDropdown>
            </li>
            <li className="nav__item">
              <NavDropdown
                title={
                  <span className="nav__link">
                    <img src="/img/batlua.png" alt="Ph kin" height="20" style={{ marginRight: "6px" }} />
                    Ph kin
                  </span>
                }
                id="basic-nav-dropdown"
              >
                <NavDropdown.Item as={Link} to="/boxclock">Hp ng h</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/leatherstrap">Dy da</NavDropdown.Item>
              </NavDropdown>
            </li>
            <li className="nav__item">
              <NavDropdown
                title={
                  <span className="nav__link">
                    <img src="/img/phukien.png" alt="Dch v" height="20" style={{ marginRight: "6px" }} />
                    Dch v
                  </span>
                }
                id="basic-nav-dropdown"
              >
                <NavDropdown.Item as={Link} to="/service">In khc</NavDropdown.Item>
              </NavDropdown>
            </li>
            <li className="nav__item">
              <Link to="/repair" className="nav__link">
                <img src="/img/suachua.png" alt="Sa cha Galle Care" height="20" />
                Sa cha
              </Link>
            </li>
            <li className="nav__item">
              <NavDropdown
                title={
                  <span className="nav__link">
                    <img src="/img/tintuc.png" alt="Tin tc" height="20" style={{ marginRight: "6px" }} />
                    Tin tc
                  </span>
                }
                id="basic-nav-dropdown"
              >
                <NavDropdown.Item as={Link} to="/knowledge">Kin thc ng h</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/news">Chng nhn thng hiu</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/newfeed">Bn tin ng h</NavDropdown.Item>
              </NavDropdown>
            </li>
            <li className="nav__item">
              <Link to="/sale" className="nav__link nav__sale">SALE</Link>
            </li>
          </ul>
        </div>
      </nav>
    </header>
  );
}

export default Header;