import React, { useState } from "react";
import { Dropdown, Modal, Button, Form } from "react-bootstrap";
import "./Account.css";

function Account() {
  const [showLogin, setShowLogin] = useState(false);
  const [showRegister, setShowRegister] = useState(false);

  return (
    <>
      {/* Nt Account Dropdown */}
      <Dropdown align="end" className="account-dropdown">
        <Dropdown.Toggle
          variant="light"
          id="dropdown-account"
          className="account-toggle"
        >
          <img
            src="https://galle.vn/images/config/account-dark_1718256807.svg"
            alt="Account"
            width="26"
            height="25"
          />
        </Dropdown.Toggle>

        <Dropdown.Menu>
          <Dropdown.Item onClick={() => setShowLogin(true)}>
            ng nhp
          </Dropdown.Item>
          <Dropdown.Item onClick={() => setShowRegister(true)}>
            To ti khon
          </Dropdown.Item>
        </Dropdown.Menu>
      </Dropdown>

      {/* Popup ng nhp */}
      <Modal show={showLogin} onHide={() => setShowLogin(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title>ng nhp</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3">
              <Form.Label>Email</Form.Label>
              <Form.Control type="email" placeholder="Nhp email" />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Mt khu</Form.Label>
              <Form.Control type="password" placeholder="Nhp mt khu" />
            </Form.Group>
            <Button variant="primary" className="w-100">
              ng nhp
            </Button>
          </Form>
        </Modal.Body>
      </Modal>

      {/* Popup ng k */}
      <Modal show={showRegister} onHide={() => setShowRegister(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title>To ti khon</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3">
              <Form.Label>H v tn</Form.Label>
              <Form.Control type="text" placeholder="Nhp h v tn" />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Email</Form.Label>
              <Form.Control type="email" placeholder="Nhp email" />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Mt khu</Form.Label>
              <Form.Control type="password" placeholder="Nhp mt khu" />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Nhp li mt khu</Form.Label>
              <Form.Control type="password" placeholder="Nhp li mt khu" />
            </Form.Group>
            <Button variant="success" className="w-100">
              ng k
            </Button>
          </Form>
        </Modal.Body>
      </Modal>
    </>
  );
}

export default Account;
