import { useContext } from 'react';
import { CartContext } from '../contexts/CartContext';
import { Col, Container, Row } from "react-bootstrap";
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import "./Watch.css";

function Watchwomen({ onAddToCart }) {
  const { addToCart } = useContext(CartContext);  const watches = [
    { id: "C4667/1", name: "ng h n Candino", type: "30 mm | ng h in t (Quartz)", price: "7.000.000", img: "img/dh7.webp" },
    { id: "C4721/1", name: "ng h N Candino Lady Elegance", type: "33 mm | ng h in t (Quartz)", price: "5.000.000", img: "img/dh8.webp" },
    { id: "C4721/2", name: "ng h N Candino Lady Elegance", type: "33 mm | ng h in t (Quartz)", price: "4.900.000", img: "img/dh9.webp" },
    { id: "C4721/4", name: "ng h n Candino", type: "33 mm | ng h in t (Quartz)", price: "4.000.000", img: "img/dh10.webp" },
    { id: "FC-200MPW2AC3B", name: "ng h N Frederique Constant Classics Art Deco Carree", type: "21 mm | ng h in t (Quartz)", price: "41.090.000", img: "img/dh14.webp" },
    { id: "FC-200MPW2AC2B", name: "ng h N Frederique Constant Classics Art Deco Carree", type: "28 mm | ng h in t (Quartz)", price: "41.089.000", img: "img/dh15.webp" },
    { id: "FC-240MPWD2NH22B", name: "ng h N Frederique Constant Highlife Ladies Quartz", type: "31 mm | ng h in t (Quartz)", price: "55.790.000", img: "img/dh16.webp" },
    { id: "C4766/3", name: "ng h Candino n Lady Elegance", type: "34 mm | ng h in t (Quartz)", price: "6.089.000", img: "img/dh10.webp" }
  ];
  return (
    <Container className="watch-list mt-4">
      <h2 className="title fw-bold">B su tp ng h n chnh hng ti ng h Galle</h2>
      <hr />
      <Row>
        {watches.map((watch) => (
          <Col key={watch.id} xs={12} sm={6} md={4} lg={3} className="mb-4">
            <Card className="watch-card h-100 shadow-sm">
              <Card.Img
                variant="top"
                src={watch.img}
                alt={watch.name}
                className="watch-img"
              />
              <Card.Body className="d-flex flex-column">
                <Card.Title className="watch-title">{watch.name}</Card.Title>
                <Card.Text className="watch-text">
                  <strong>M:</strong> {watch.id} <br />
                  <p>{watch.type}</p>
                  <strong>Gi:</strong> {watch.price}
                </Card.Text>
                <div className="mt-auto">
                  <Button
                    variant="primary"
                    className="watch-btn"
                    onClick={() => {
                      addToCart(watch);
                      if (onAddToCart) onAddToCart();
                    }}
                  >
                    Thm vo gi
                  </Button>
                </div>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export default Watchwomen;